import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Alert, Button, Card, CardBody, CardHeader, Col, CustomInput, Row } from 'reactstrap';
import { EditForm, EditViewProps, ERROR, FileInputPreview, InputGroupInline, InputType, showToast, WindowComponent } from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { CertificadoDigital, CLASS_NAME } from '../../../../models/dtos/ger/certificadoDigital';
import { CertificadoDigitalParam } from '../../../../models/dtos/ger/certificadoDigitalParam';
import { findConfigCertificadoDigital, validarCertificadoDigital } from '../../../../services/ger.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import ResponsavelIS from '../../../components/ger/responsavelIS/ResponsavelIS';

interface CertificadoDigitalView extends EditViewProps<CertificadoDigital> {}

const CertificadoDigitalView: FC<CertificadoDigitalView & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { reset, ...formProps } = useForm<CertificadoDigital>({ mode: 'onBlur' });

  const [certificadoATres, setCertificadoATres] = useState();
  const [isResponsavelDisabled, setIsResponsavelDisabled] = useState();
  const [isProcEletronicaSelected, setIsProcEletronicaSelected] = useState();
  const [certificadoAtual, setCertificadoAtual] = useState();

  const onChangeCertificadoAUm = () => {
    formProps.setValue('certificadoA3', !formProps.getValues().certificadoA1);
    setCertificadoATres(!formProps.getValues().certificadoA1);
  };

  const onChangeCertificadoATres = () => {
    formProps.setValue('certificadoA1', !formProps.getValues().certificadoA3);
    setCertificadoATres(formProps.getValues().certificadoA3);
  };

  const onChangeCNPJ = () => {
    formProps.setValue('usarCpfResponsavelProcuracao', !formProps.getValues().usarCnpjResponsavelProcuracao);
  };

  const onChangeCPF = () => {
    formProps.setValue('usarCnpjResponsavelProcuracao', !formProps.getValues().usarCpfResponsavelProcuracao);
  };

  const onChangeProcuracaoEletronica = () => {
    setIsProcEletronicaSelected(formProps.getValues().isProcuracaoEletronica);
    formProps.setValue('isAdministradoraProcuracao', formProps.getValues().isProcuracaoEletronica);
  };

  const onChangeAdm = () => {
    setIsResponsavelDisabled(!formProps.getValues().isProcuracaoEletronica || formProps.getValues().isAdministradoraProcuracao);
  };

  useEffect(
    () => {
      findConfigCertificadoDigital(globalReducer.globalParameter.empresa.id, {
        thenFunction: (result: CertificadoDigital) => {
          if (result) {
            setCertificadoAtual(result.arquivoCertificadoDigital);
            setCertificadoATres(result.certificadoA3);
            result.certificadoA1 = !result.certificadoA3;
            result.usarCnpjResponsavelProcuracao = !result.usarCpfResponsavelProcuracao;
            reset(result);
          } else {
            reset({
              certificadoA1: true,
              notificacaoCertificado: true,
              notificacaoNroDiasCertificadoVencer: 30,
              usarCnpjResponsavelProcuracao: true,
            });
            setCertificadoATres(false);
          }
          setIsProcEletronicaSelected(result.isProcuracaoEletronica);
        },
      });
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  useEffect(
    () => {
      formProps.setValue('isAdministradoraProcuracao', formProps.getValues().isProcuracaoEletronica);
      setIsResponsavelDisabled(!formProps.getValues().isProcuracaoEletronica || formProps.getValues().isAdministradoraProcuracao);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [formProps.getValues().isProcuracaoEletronica]
  );

  useEffect(
    () => {
      if (isResponsavelDisabled) {
        formProps.setValue('responsavelProcuracao', null);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [isResponsavelDisabled]
  );

  const removerCertificado = () => {
    setCertificadoAtual(undefined);
  };

  const validateExtras = () => {
    const { isProcuracaoEletronica, isAdministradoraProcuracao, responsavelProcuracao } = formProps.getValues();

    const erros = [];

    if (isProcuracaoEletronica && !isAdministradoraProcuracao && !responsavelProcuracao) {
      erros.push(
        "Quando a opção 'Procuração Eletrônica' for selecionada, deve-se marcar a opção 'Administradora Responsável' ou selecionar um Responsável caso não seja a Administradora."
      );
    }

    return erros;
  };

  const beforePersist = (formData: CertificadoDigital, done: any) => {
    formData.empresa = globalReducer.globalParameter.empresa;
    if (formData.certificadoA3) {
      formData.senhaCertificadoDigital = null;
      formData.notificacaoCertificado = false;
      formData.arquivoCertificadoDigital = null;
      formData.notificacaoNroDiasCertificadoVencer = 30;
      formData.nomeCertificadoDigital = null;
      done(formData);
    } else {
      validarCertificadoDigital(formData, {
        thenFunction: (result: CertificadoDigitalParam) => {
          if (result.ruleMessage.success) {
            formData.arquivoCertificadoDigital = result.arquivo;
            if (formData.arquivos) {
              formData.nomeCertificadoDigital = formProps.getValues().arquivos[0].nomeArquivo;
            }
            done(formData);
          } else {
            showToast(result.ruleMessage.message, ERROR);
          }
        },
      });
    }
    return { async: true };
  };

  return (
    <WindowComponent
      title="Configuração Certificado Digital"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter)]}
      wikiId={3207}
    >
      <EditForm
        hideCancelButton={true}
        formProps={formProps}
        editViewProps={props}
        dtoClassName={CLASS_NAME}
        beforePersist={beforePersist}
        validateExtras={validateExtras}
      >
        <Card>
          <CardHeader className="font-weight-bold bg-light">Tipo de Certificado Digital</CardHeader>
          <CardBody>
            <Row>
              <Col>
                <CustomInput
                  type="switch"
                  label="Certificado A1"
                  name="certificadoA1"
                  id="certificadoA1"
                  innerRef={formProps.register}
                  onChange={onChangeCertificadoAUm}
                />
              </Col>
              <Col>
                <CustomInput
                  type="switch"
                  label="Certificado A3"
                  name="certificadoA3"
                  id="certificadoA3"
                  innerRef={formProps.register}
                  onChange={onChangeCertificadoATres}
                />
              </Col>
            </Row>
          </CardBody>
        </Card>
        <br />
        {!certificadoATres && (
          <>
            <Card>
              <CardBody>
                {certificadoAtual ? (
                  <>
                    <InputGroupInline id="arquivoCertificadoDigital" formProps={formProps} hidden={true} />
                    <Row>
                      <Col>
                        <InputGroupInline
                          id="nomeCertificadoDigital"
                          formProps={formProps}
                          isPrependLabel={true}
                          label="Certificado Digital"
                          disabled={true}
                        />
                      </Col>
                      <Col>
                        <Button className="btn-danger" onClick={removerCertificado}>
                          Remover Certificado
                        </Button>
                      </Col>
                    </Row>
                  </>
                ) : (
                  <Row>
                    <Col>
                      <FileInputPreview
                        acceptedTypes=".pfx"
                        formProps={formProps}
                        isMultiple={false}
                        limitSizeBytes={204800}
                        propImageName="arquivos"
                      />
                    </Col>
                  </Row>
                )}
                <br />
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { required: true, maxLength: 150 } }}
                      id="senhaCertificadoDigital"
                      isPrependLabel={true}
                      label="Senha Certificado Digital"
                      placeholder="Informe a Senha"
                      type={InputType.PASSWORD}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
            <br />
            <Card>
              <CardHeader className="font-weight-bold bg-light">Notificações</CardHeader>
              <CardBody>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { required: true, min: 1, max: 60 } }}
                      id="notificacaoNroDiasCertificadoVencer"
                      isPrependLabel={true}
                      label="Nº de dias para notificação de certificado a vencer"
                      placeholder="Informe o número de dias"
                      type={InputType.NUMBER}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <CustomInput
                      type="switch"
                      label="Notificar Certificado Digital a vencer"
                      name="notificacaoCertificado"
                      id="notificacaoCertificado"
                      innerRef={formProps.register}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
            <br />
          </>
        )}
        {certificadoATres && (
          <>
            <Alert color="danger" className="text-center">
              ATENÇÃO
              <br />
              Para utilizar o Certificado Digital do tipo A3 é necessário efetuar o download do aplicativo abaixo. O aplicativo deve ser instalado na
              mesma máquina onde se encontra o Certificado Digital. O Aplicativo irá se encarregar de assinar e transmitir os arquivos digitalmente.
            </Alert>
            <div className="text-center">
              <a
                href="https://download.projetusti.com.br/calima/eSocialA3-install.exe"
                className="btn btn-primary"
                target="_blank"
                rel="noopener noreferrer"
              >
                Baixar Aplicativo
              </a>
            </div>
            <br />
          </>
        )}
        <Card>
          <CardHeader className="font-weight-bold bg-light">Procuração Eletrônica</CardHeader>
          <CardBody>
            <Row>
              <Col>
                <CustomInput
                  type="switch"
                  label="Procuração Eletrônica"
                  name="isProcuracaoEletronica"
                  id="isProcuracaoEletronica"
                  innerRef={formProps.register}
                  onChange={onChangeProcuracaoEletronica}
                />
              </Col>
            </Row>
            <Row>
              <Col>
                <CustomInput
                  type="switch"
                  label="Administradora Geral do Sistema é a Responsável"
                  name="isAdministradoraProcuracao"
                  id="isAdministradoraProcuracao"
                  innerRef={formProps.register}
                  onChange={onChangeAdm}
                  disabled={!isProcEletronicaSelected}
                />
              </Col>
            </Row>
            <br />
            <Row>
              <Col>
                <ResponsavelIS
                  id="responsavelProcuracao"
                  formProps={formProps}
                  label="Responsável"
                  placeholder="Selecione um Responsável"
                  isPrependLabel={true}
                  disabled={isResponsavelDisabled}
                />
              </Col>
            </Row>
            <Card>
              <CardHeader className="font-weight-bold bg-light">Informação do Responsável que será usada na procuração</CardHeader>
              <CardBody>
                <Row>
                  <Col>
                    <CustomInput
                      type="switch"
                      label="CNPJ"
                      name="usarCnpjResponsavelProcuracao"
                      id="usarCnpjResponsavelProcuracao"
                      innerRef={formProps.register}
                      onChange={onChangeCNPJ}
                      disabled={!isProcEletronicaSelected}
                    />
                  </Col>
                  <Col>
                    <CustomInput
                      type="switch"
                      label="CPF"
                      name="usarCpfResponsavelProcuracao"
                      id="usarCpfResponsavelProcuracao"
                      innerRef={formProps.register}
                      onChange={onChangeCPF}
                      disabled={!isProcEletronicaSelected}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </CardBody>
        </Card>
        <br />
      </EditForm>
    </WindowComponent>
  );
};

export default CertificadoDigitalView;
